import {Avatar, Grid, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {CoineusUSDFormat, CoineusCryptoFormat} from "../utils/currency_format";
import clsx from "clsx";
import {makeStyles} from "@mui/styles";

import TokenInfoDialog from "./TokenInfoDialog";
import {useEffect, useState} from "react";
import {useSelector} from "react-redux";
import {BigNumber} from "bignumber.js";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        },
        '&.eth': {
            backgroundImage: `url('https://coineus.app/assets/tokens/eth/logo.png')`,
        }
    }
}));

export default function LPTokenListItem(props) {

    const classes = useStyles();

    const {wallet} = useSelector(state => state.coineus);

    const {contract_address, token0, token1, token0Symbol, token1Symbol, name, network, price, wrap, cnsDetails, secondaryAction} = props;

    const [open, setOpen] = useState(false);

    if (wallet.balances[network] === undefined) {
        return <></>;
    }

    if (wallet.lp[network][contract_address] === undefined) {
        return <></>;
    }

    if (wallet.balances[network][contract_address] < .01) {
        return <></>;
    }


    const getCNSEarned = () => {
        const {balance, deposits, transfersIn, transfersOut, withdrawals, fees} = cnsDetails;

        const bal = new BigNumber(0).plus(balance);

        const sub = new BigNumber(0).plus(deposits).plus(transfersIn).minus(withdrawals).minus(transfersOut).minus(fees);

        return bal.minus(sub).dividedBy(10**18);
    }

    return <Paper style={{marginTop: 10, marginBottom: 5}}>
        <ListItem secondaryAction={secondaryAction}>
            <ListItemAvatar onClick={() => setOpen(true)}>
                <Avatar
                    style={{marginLeft:-5}}
                    src={`https://coineus.app/assets/tokens/${network}/${token0}/logo.png`}/>
                <Avatar
                    style={{marginTop: -40, marginLeft:20, marginRight: 20}}
                    src={`https://coineus.app/assets/tokens/${network}/${token1}/logo.png`}/>
            </ListItemAvatar>
            <ListItemText
                primary={name}
                // secondary={CoineusUSDFormat(price?.inUSD)}
                secondary="LP Token"
                primaryTypographyProps={{fontWeight: 700}}
            />
            <ListItemText
                primary={CoineusCryptoFormat(wallet.balances[network][contract_address])}
                secondary={CoineusUSDFormat(price?.inUSD * wallet.balances[network][contract_address])}
                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                secondaryTypographyProps={{textAlign: 'right'}}
            />
        </ListItem>

        <ListItem>
            <ListItemText
                primary={CoineusCryptoFormat(wallet.lp[network][contract_address].token0)}
                secondary={token0Symbol}
                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                secondaryTypographyProps={{textAlign: 'right'}}
            />
            <ListItemText
                primary={CoineusCryptoFormat(wallet.lp[network][contract_address].token1)}
                secondary={token1Symbol}
                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                secondaryTypographyProps={{textAlign: 'right'}}
            />
            <ListItemText
                primary={wallet.lp[network][contract_address].share.toFixed(2) + " %"}
                secondary="Pool Share"
                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                secondaryTypographyProps={{textAlign: 'right'}}
            />
        </ListItem>




        {/*TODO work on token details*/}
        <TokenInfoDialog
            contract_address={contract_address}
            wrap={wrap}
            network={network}
            open={open}
            close={() => setOpen(false)}/>
    </Paper>
}